@import url(https://fonts.googleapis.com/css?family=Merriweather:400,400i,900|Montserrat:400,400i,700);
@import url(https://use.fontawesome.com/releases/v5.8.1/css/all.css);
/* font import */

/* fontawesome */

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(58, 63, 66);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
h1{
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  padding: 2em;
  
}


/* thank you internet for this <3 */
.header-h1 {
  -webkit-animation: color_change 1s infinite alternate;
  animation: color_change 1s infinite alternate;
}
@-webkit-keyframes color_change {
	from { color: hotpink; }
	to { color: rgb(229, 255, 0); }
}
textarea {
  font-family: "Merriweather", serif;
  font-style: italic;
}

h3 {
  font-family: "Montserrat", sans-serif;
  color: white;
  font-weight: 400;
  padding-top: 1em;
}

input {
  margin: 0.5em;
  font-family: "Merriweather", serif;
  padding: 7.2px 14.4px;
  border-radius: 3px;
  border: none;
}

input:focus {
  background-color: rgb(245, 222, 180);
}

input::-webkit-input-placeholder {
  font-family: "Merriweather", serif;
  font-style: italic;
}

input::-moz-placeholder {
  font-family: "Merriweather", serif;
  font-style: italic;
}

input::-ms-input-placeholder {
  font-family: "Merriweather", serif;
  font-style: italic;
}

input::placeholder {
  font-family: "Merriweather", serif;
  font-style: italic;
}

button {
  font-family: "Montserrat", sans-serif;
  margin-top: 1em;
  background-color: rgb(86, 94, 99);
  border: none;
  color: white;
  padding: 18px 36px;
  text-decoration: none;
  cursor: pointer;
  border-radius: 3px;
}

button:hover {
  color: black;
  -webkit-animation: background_color_change 1s infinite alternate;
  animation: background_color_change 1s infinite alternate;
}

@-webkit-keyframes background_color_change {
  from {
    background-color: hotpink;
  }
  to {
    background-color: rgb(229, 255, 0);
  }
}

h4,
h2{
  color: white;
  font-weight: 400;
}

h4{
  font-family: 'Merriweather', serif;
}

h2{
  padding-top: 1em;
  font-family: 'Montserrat', sans-serif;
}

.error-paragraph {
  color: white;
  font-style: italic;
}
.App {
  text-align: center;
  /* background: linear-gradient(#075d63, #275053); */
  background-color: rgb(58, 63, 66);
  min-height: 100vh;
}
.App-header {  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* font-size: calc(10px + 2vmin); */
  color: white;
}
